body {
  margin: 0;
  padding: 0;
  font-family: Calibre, system, -apple-system, BlinkMacSystemFont, Roboto,
  "Helvetica Neue", "Segoe UI", Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.AppBar {
  background-color: #FFF;
  color: #000;
  max-height: 50px;
  padding: 20px;
  display: flex;
  position: relative;
  top: 0;
}

.PrimaryAction {
  display: flex;
  justify-content: flex-end;
}

.PrimaryFont {
  font-size: 20px;
}

.sidenav {
  height: 100%;
  width: 260px;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: #2f3542;
  overflow-x: hidden;
  padding-top: 20px;
}

.sidenav a {
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  font-size: 25px;
  color: #dfe6e9;
  display: block;
}

.sidenav a:hover {
  color: #f1f1f1;
}

img {
  display: block;
  width: 150px;
  height: 150px;
  margin: 1em auto;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  -webkit-border-radius: 99em;
  -moz-border-radius: 99em;
  box-shadow: 0 3px 2px rgba(0, 0, 0, 0.3);
}
